.NavigationItem{
    margin: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
   
}

.NavigationItem a{
    color: #8F5C2C;
    text-decoration: none;
    width: 100%;
   
    -webkit-box-sizing: border-box;
   
            box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    
    color: #40A4CB;

}

@media (min-width:500px){
    .NavigationItem{
        margin: 0;
        width: auto;
        display: -ms-flexbox;
        display: flex;
        height: 100%;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavigationItem a{
        color: white;
       
        height: 100%;
        padding: 16px 10px;
        border-bottom: 3px solid transparent;
     
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        background-color: #8F5C2C;
        border-bottom: 3px solid #40A4CB;
        color: white;
    
    }
}


