.Toolbar{
    width: 100%;
    height: 56px;
    position: fixed;
    z-index: 90;
    left: 0;
    top: 0;
    background-color: #703B09;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 20px;

}
.Toolbar nav{
    height: 100%;
}
.Logo{
    height:80%;
}
@media (max-width:499px){
    .DesktopOnly{
        display: none;
    }
}


